.main_tour {
    background-image: linear-gradient(rgba(255, 255, 255, 0.7), rgba(0, 0, 0, 0.7)), url("../../public/img/seljalandsfoss-1751463.jpg");

}

.container_card {
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100vw;
    margin: 10px;
}

.main_info_card{
    width: 25vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.name {
    font-family: 'Merriweather', serif;
    font-size: 32px;
    margin: 15px;
}

.text {
    font-family: 'Merriweather', serif;
    font-size: 24px;
    text-align: justify;
    max-width: 70%;
}

.image {
    width: 50%;
    height: 45%;
    border-radius: 12px;
}

.button_book {
    border-radius: 15px;
    background-color: aliceblue;
    color: #000000;
    width: 80%;
    height: 15%;
    font-family: 'Merriweather', serif;
    font-size: 24px;
    margin: 30px;
}

.alert_disable{
    display: none;
}

.alert_active{
    display: block;
    max-width: 60%;
    margin: 10px;
}

.active_loader{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
