.main{
    overflow-x: hidden;

}

.container_change{

    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 100vw;
    overflow-x: hidden;
}

.image_for_change{
    margin: 15px;
    border-radius: 12px;
    width: 30vw;
    height: 35vh;
    position: static;
}

.img_container{
    position: relative;
}

.text_on_image {
    font-size: 32px;
    font-family: 'Merriweather', serif;
    position: absolute;
    top: 12px;
    left: 20px;
}

.activeTour{
    display: block;
    font-size: 32px;
    font-family: 'Merriweather', serif;
    color: white;
}

.disableTour{
   display: none;
}


.page_disable{
    display: none;
}
