.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.active {
    opacity: 1;
    pointer-events: all;
}

.modal_content {
    width: 50vw;
    font-size: 24px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    background-color: white;
    transform: scale(0.5);
    transition: 0.4s all;
    padding: 70px;
}

.modal_content.active {
    transform: scale(1);
}

.auth_text {
    margin: 10px;
    font-family: 'Merriweather', serif;

}

.auth_input {
    margin: 10px;
    border-radius: 12px;
    font-family: 'Merriweather', serif;
    width: 50%;
    height: 5vh;
}

.error {
    border: solid red;
}

.button {
    border-radius: 12px;
    width: 50%;
    height: 10vh;
    background-color: black;
    color: white;
}


.auth_label{
    font-family: 'Merriweather', serif;
    opacity: 0;
    font-size: 14px;
    color: red;
}

.auth_label_active{
    opacity: 1;
}

.disable_link{
    pointer-events: none;
}

.active_link{
    pointer-events: auto;
}
