.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: linear-gradient(rgba(255, 255, 255, 0.7), rgba(0, 0, 0, 0.7)), url("../../public/img/seljalandsfoss-1751463.jpg");
    background-size: cover;
    height: 100vh;
    margin: 0;
    overflow-x: hidden;
    overflow-y: hidden;

}

.con {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.vstup {
    color: white;
    margin: 5px;
    font-size: 24px;
    font-family: 'Merriweather', serif;
    max-width: 700px;
    background-color: rgb(51,36,36,0.7);
    padding: 25px;
    border-radius: 15px;
    max-height: 40%;
}
